import React from "react";
import { ReactComponent as SettingIcon } from "../icons/setting.svg";

const SideBarRight = () => {
  return (
    <div className="w-full flex flex-col">
      {Array(6)
        .fill()
        .map((_, idx) => (
          <div className="relative flex items-center justify-center group p-2 py-4 hover:bg-five transition-all duration-700 cursor-pointer">
            <SettingIcon className="w-5 h-5" />
            <div className="absolute transition-all duration-700 w-1 right-0 bg-two h-0 bottom-0 group-hover:h-full"/>
          </div>
        ))}
    </div>
  );
};

export default SideBarRight;
